@font-face {
  font-weight: 400;
  font-family: "PTMono";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "RobotoCondensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  padding: 0;
  margin: 0;
  font-family: "PTMono", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #111111;
  background-color: #ffffff;
  counter-reset: section; }

a {
  color: inherit;
  text-decoration: none; }
  a:hover, a:focus {
    opacity: 0.5; }

ul {
  list-style: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  margin: 0 auto;
  width: 100%; }

.page-header {
  width: 100%; }

.user-nav {
  width: 100%; }

.user-nav__container {
  max-width: 950px;
  margin: 0 auto;
  padding: 10px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  line-height: 16px; }

.user-nav__user-list {
  margin: 0;
  padding: 0;
  list-style: none;
  border: none;
  width: 100px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.user-nav__direction p {
  margin: 0;
  padding: 0; }

.user-nav__tel {
  margin: 0;
  padding: 0; }
  .user-nav__tel::before {
    content: "";
    width: 25px;
    height: 24px;
    margin-left: 5px;
    background-image: url("../img/icon-mobile.svg");
    background-color: #111111; }

.user-nav__cart-container {
  display: flex; }

.user-nav__cart a {
  margin-left: 15px; }

.user-nav__count {
  padding-left: 5px;
  color: #b98348;
  border-left: 1px solid #b98348; }

.main-nav {
  width: 100%;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #111111; }

.main-nav__container {
  max-width: 950px;
  margin: 0 auto;
  padding: 10px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.main-nav__logo {
  margin-right: 5%; }
  .main-nav__logo:hover, .main-nav__logo:focus {
    opacity: 1; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  border: none;
  width: 800px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-right: auto;
  font-family: "RobotoCondensed", "Arial Narrow", sans-serif; }
  .main-nav__list li {
    margin-left: 10px; }

.promo {
  font-family: "RobotoCondensed", "Arial Narrow", sans-serif;
  font-size: 48px;
  line-height: 54px;
  color: #ffffff;
  background-image: url("https://picsum.photos/id/1059/1920/500/?blur");
  background-size: cover; }

.promo__img-container {
  max-width: 950px;
  margin: 0 auto;
  padding: 40px 25px; }

.promo p {
  position: relative;
  width: 500px;
  margin-right: auto;
  padding: 40px 0;
  display: flex;
  text-align: center; }
  .promo p::before {
    position: absolute;
    content: "";
    top: 0;
    right: 40%;
    width: 160px;
    height: 3px;
    background-color: #ffffff; }
  .promo p::after {
    position: absolute;
    content: "";
    bottom: 0;
    right: 40%;
    width: 160px;
    height: 3px;
    background-color: #ffffff; }
